.main ul li {
  list-style-type: none;
  padding-right: 10px;
}

.main ul li a {
  display: block;
  margin: 10px 0;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  color: black;
  transition: .2s;
}

.main ul li a:hover {
  transform: translateX(20px);
  transition-delay: .1s;
}

.main ul li .active {
  transform: translateX(0);
}

.main li:nth-child(1) .active {
  border-left: rgb(4, 4, 247) solid 5px;
}

.main li:nth-child(2) .active {
  border-left: teal solid 5px;
}

.main li:nth-child(3) .active {
  border-left: red solid 5px;
}

.main li:nth-child(4) .active {
  border-left: green solid 5px;
}

.main ul li .active:hover {
  transform: translateX(0);
}