section h1.article {
  text-align: center;
  margin: 20px 0;
  font-weight: bold;
}

div.article {
  margin: 30px 20px;
  border-bottom: gray dashed 2px;
}

div .article h2,
em,
p {
  padding: 10px 0;
}

textarea {
  display: block;
  min-width: 200px;
  min-height: 100px;
  max-width: 500px;
  max-height: 300px;
  font-size: 16px;
  overflow: hidden;
}

div.contact h2 {
  margin: 10px 0;
}

div.contact button {
  margin: 10px 0;
  padding: 5px 15px;
  cursor: pointer;
}

div.loginPanel {
  margin: 20px 0;
}

div.loginPanel input {
  display: block;
  margin: 10px 0 10px 0;
}

div.productsPage h2 {
  margin: 20px 0;
}

div.productsPage li {
  margin: 10px 20px;
  color: blue;
  list-style-type: square;
}