* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Lato';
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
}

header {
  flex-basis: 20vh;
}

main {
  display: flex;
  flex-grow: 1;
}

aside {
  min-width: 20%;
}

section {
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 5px 0;
  text-align: center;
  text-transform: uppercase;
}